/* ======================================= media queries for medium decives */

@media screen and (max-width: 1024px) {
    .container{
        width: var(--container-width-md);

    }

    section{
        margin-top: 6rem;
    }
    section{
      margin-left: 10%;
      margin-right: 10%;
    } 
}

/* ======================================= media queries for small decives */

@media screen and (max-width: 600px) {
    section{
      margin-left: 2%;
      margin-right: 2%;
    } 
    .container{
        max-width: var(--container-width-sm) !important;
        /* width: 90%; */
    }

    h1{
        font-size: 2rem;
    }

    section{
        margin-top: 2rem;
    }
    .sm-none{
        display: none;
    }
    
}

